@import "../../global.scss";

.topbar{
	width: 100%;
	height: 70px;
	background-color: white;
	color: $maincolor;
	position:fixed;
	top:0;
    z-index: 3;
	transition: all 1s ease;
	.wrapper{
		padding: 10px 30px;
		display: flex;
		align-items: center;
		justify-content: space-between;

		.left{
			display: flex;
			align-items:center;

			.logo{
				font-size: 36px;
				font-weight: 500;
				text-decoration: none;
				color: inherit;
				margin-right: 30px;
			}

			.itemContainer{
				display:flex;
				align-items:center;
				margin-right: 30px;

				.icon{
					font-size: 24px;
					margin-right: 5px;
				}
				span{
					font-size: 20px;
					font-weight: 500;
				}
			}
		}

		.right{

			.hamburger{
				width: 32px;
				height: 25px;
				display: flex;
				flex-direction: column;
				justify-content:space-between;
				cursor: pointer;
			}

			span{
				width: 100%;
				height: 3px;
				background-color: $maincolor;
				transform-origin: left;
				transition: all 2s ease;
			}
		}
		}
	&.active{
		background-color: $maincolor;
		color: white;

		.hamburger{
			span{
				&:first-child{
					background-color: white;
					transform: rotate(45deg);
				}
				&:nth-child(2){
					opacity: 0;
				}
				&:last-child{
					background-color: white;
					transform: rotate(-45deg);
				}
			}
		}
	}
		@media screen and (max-width: 700px) {
			width: 100%;
			height: 70px;
			background-color: white;
			color: $maincolor;
			position:fixed;
			top:0;
		  z-index: 3;
			transition: all 1s ease;
			.wrapper{
				padding: 10px 30px;
				display: flex;
				align-items: center;
				justify-content: space-between;

				.left{
					display: flex;
					align-items:center;

					.logo{
						font-size: 36px;
						font-weight: 500;
						text-decoration: none;
						color: inherit;
						margin-right: 30px;
					}

					.itemContainer{
						display:flex;
						align-items:center;
						margin-right: 30px;

						.icon{
							font-size: 24px;
							margin-right: 5px;
						}
						span{
							display: none;
						}
					}
				}

				.right{

					.hamburger{
						width: 32px;
						height: 25px;
						display: flex;
						flex-direction: column;
						justify-content:space-between;
						cursor: pointer;
					}

					span{
						width: 100%;
						height: 3px;
						background-color: $maincolor;
						transform-origin: left;
						transition: all 2s ease;
					}
				}
				}
			&.active{
				background-color: $maincolor;
				color: white;

				.hamburger{
					span{
						&:first-child{
							background-color: white;
							transform: rotate(45deg);
						}
						&:nth-child(2){
							opacity: 0;
						}
						&:last-child{
							background-color: white;
							transform: rotate(-45deg);
						}
					}
				}
			}
		}
}
